<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="transmit-page create-action-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
    :pullDownRefresh="true"
    :pullUpLoad="true"
    @on-refresh="onRefresh"
    @on-show="onShow"
    @on-load="onLoad"
  >
    <div slot="header">
      <van-nav-bar id="header" title="智能维修">
        <!-- <van-icon name="ellipsis" slot="right" @click.native="onMoreAction" /> -->
        <i class="icon-wode nav-icon-right" slot="right" @click="handleToMe"></i>
      </van-nav-bar>

      <van-popup v-model="moreAction" class="header-more" :style="{top: moreTop + 'vw'}">
        <div class="more-action">
          <van-cell title="提报故障" clickable @click.native="handleCreateFault" />
          <van-cell title="提报问题" clickable @click.native="handleCreateProblem" />
        </div>
      </van-popup>

      <div class="search-bar">
        <van-search placeholder="搜索工单编号" v-model="filter.key" />
      </div>

      <van-dropdown-menu class="select-bar">
        <van-dropdown-item v-model="filter.status" :options="statusOptions" />
        <van-dropdown-item v-model="filter.source" :options="sourceOptions" />
        <van-dropdown-item :title="filter.time" ref="time">
          <van-datetime-picker
            v-model="time"
            type="year-month"
            @cancel="onTimeCancel"
            @confirm="onTimeConfirm"
          />
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>

    <van-loading type="spinner" v-if="loading" class="page-loading" />

    <transition-group class="transmit-list" tag="div" name="list">
      <item 
        @click.native="handleDetail(item)"
        v-for="(item) in list"
        :key="item.id"
        :data="item"
      >
      </item>
    </transition-group>

    <template slot="blank">
      <div class="fault-action" @click="handleCreateFault"></div>
      <div class="problem-action" @click="handleCreateProblem"></div>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import Item from './Item'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'
import {transmitStatusEnum, transmitSourceEnum} from '../../utils/enum.js'
import moment from 'moment'

export default {
  components: {
    Page,
    Item,
    // AScroll
  },
  data: () => ({
    headerHeightVW: 66 / 3.75,
    moreAction: false,
    moreTop: 0,
    header: null,
    filter: {
      key: '',
      status: '',
      source: '',
      pageIndex: 1,
      pageSize: 10,
      time: moment().format('YYYY-MM')
    },
    transmitStatusEnum: transmitStatusEnum,
    transmitSourceEnum: transmitSourceEnum,
    list: [],
    loading: true,
    statusOptions: [
      {text: '全部', value: ''},
      {text: '待确认', value: 'to_confirmed'},
      {text: '已接受', value: 'accepted'},
      {text: '已拒绝', value: 'rejected'},
    ],
    sourceOptions: [
      {text: '全部', value: ''},
      {text: '我发起的', value: '1'},
      {text: '我收到的', value: '2'}
    ],
    time: new Date(),
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      console.log(JSON.stringify(header))
      console.log(JSON.stringify(main))

      this.headerHeightVW = (this.header.h + 95) * 100 / this.header.w
      this.moreTop = this.header.h * 100 / this.header.w

      this.onRefresh()
    },

    onMoreAction () {
      this.moreAction = true
    },

    onShow () {
      // this.onRefresh()
      if (localStorage.getItem('refresh_transmit_id')) {
        io.getTransmitListId({id: localStorage.getItem('refresh_transmit_id')}).then(data => {
          
          const index = this.list.findIndex(item => item.id == data.detail.id)

          if (index >= 0) {
            this.$set(this.list, index, data.detail)
          }

          localStorage.setItem('refresh_transmit_id', '')
        })
      }
    },

    onRefresh () {
      this.filter.pageIndex = 1

      io.getTransmitList(this.filter).then(data => {
        this.list = data.list
        this.filter.pageIndex++
        this.$refs.page.finishRefresh()
        this.loading = false
      }).catch(e => {
        this.$toast(e)
        this.$refs.page.finishRefresh()
        this.loading = false
      })
    },

    onLoad () {
      io.getTransmitList(this.filter).then(data => {
        this.list = this.list.concat(data.list)
        this.filter.pageIndex++
        if (data.list.length == 0) {
          this.$refs.page.endLoad()
        } else {
          this.$refs.page.updateLoad()
        }
        this.loading = false
      }).catch(e => {
        this.$toast(e)
        this.$refs.page.updateLoad()
        this.loading = false
      })
    },

    handleDetail (item) {
      localStorage.setItem('refresh_transmit_id', item.id)
      navigate.open('transmitdetail', {id: item.id})
    },

    onTimeCancel () {
      this.$refs.time.toggle()
    },

    onTimeConfirm (val) {
      console.log(val)
      this.filter.time = moment(val).format('YYYY-MM')
      this.$refs.time.toggle()
      this.onRefresh()
    },

    handleCreateFault () {
      this.moreAction = false
      navigate.open('createFault')
    },

    handleCreateProblem () {
      this.moreAction = false
      navigate.open('createProblem')
    },

    handleToMe () {
      navigate.open('me')
    }

  },
  watch: {
    'filter.status': function (val) {
      this.onRefresh()
    },
    'filter.source': function (val) {
      this.onRefresh()
    }
  }
}
</script>

